.record {
  margin: 20px;
}

.record img {
  height: 80px;
}

.record-body {
  border: 1px solid #DDD;
  border-radius: 10px;
  padding: 20px;
  -webkit-transition: background-color 0.2s, height, 0.2s;
  -o-transition: background-color 0.2s, height, 0.2s;
  transition: background-color 0.2s, height, 0.2s;
}

.record-body:hover {
  background-color: #EEE;
}

.unread {
  color: orange;
}



